import { graphql } from 'gatsby';
import React, { useCallback, useState } from 'react';
import CategoryAside from '../components/categoryAside';
import PostCardList from '../components/PostCardList';
import Seo from '../components/Seo';
import Layout from '../layout';
import '../styles/global.css';

const IndexPage = ({ data }) => {
  const categories = ['Ï†ÑÏ≤¥', ...data.site.siteMetadata.postBasisCategories];
  const posts = data.allMarkdownRemark.edges.map(({ node }) => ({
    id: node.id,
    postSlug: node.fields.postSlug,
    title: node.frontmatter.title,
    createdAt: node.frontmatter.createdAt,
    content: node.excerpt,
    category: node.frontmatter.categories.split(' ')
  }));

  const postCategoryCount = [
    posts.length,
    ...categories.slice(1).map((el) => 0)
  ];
  posts.forEach((post) => {
    categories.forEach((category) => {
      if (post.category.includes(category)) {
        postCategoryCount[categories.indexOf(category)] += 1;
      }
    });
  });

  const [curCategory, setCurCategory] = useState(categories[0]);
  const setActiveCategory = useCallback((value) => {
    setCurCategory(value);
  }, []);

  return (
    <Layout>
      <Seo
        title={`ÌïúÍ≤∞ Î∏îÎ°úÍ∑∏ | ${curCategory}`}
        description={`PM Í∞ïÌïúÍ≤∞Ïùò Î∏îÎ°úÍ∑∏ÏûÖÎãàÎã§.`}
      />
      <main className='w-full px-3 flex md:flex-row md:gap-x-4 md:gap-y-0 justify-between sm:min-h-[950px]'>
        <section className='md:basis-2/3'>
          <PostCardList posts={posts} category={curCategory} />
        </section>
        <section className='md:basis-1/3 hidden md:block'>
          <CategoryAside
            id='category-aside'
            categories={categories}
            setActiveCategory={setActiveCategory}
            category={curCategory}
            postCategoryCount={postCategoryCount}
          />
        </section>
      </main>
    </Layout>
  );
};

export default IndexPage;

export const siteQuery = graphql`
  query {
    site {
      siteMetadata {
        postBasisCategories
      }
    }

    allMarkdownRemark(
      filter: { fields: { postSlug: { ne: null } } }
      sort: { frontmatter: { createdAt: DESC } }
    ) {
      edges {
        node {
          fields {
            postSlug
          }
          id
          excerpt(pruneLength: 300, truncate: true)
          frontmatter {
            createdAt
            title
            categories
          }
        }
      }
    }
  }
`;
