import React from 'react';

const CategoryAside = ({
  categories,
  setActiveCategory,
  category,
  postCategoryCount
}) => {
  const changeCurCategory = (e) => {
    e.preventDefault();
    setActiveCategory(e.currentTarget.id);
  };

  return (
    <section className='categoryAside-container'>
      <div className='flex flex-col w-full' id='category-header'>
        <p className='text-sm sm:text-lg md:text-xl font-medium md:font-semibold'>
          {category}
        </p>
        <hr className='w-full border-[#343a40] mt-3 md:mt-5' />
      </div>
      <div
        className='w-full flex md:flex-col gap-x-2 gap-y-2 flex-wrap'
        id='category-list'>
        {categories.map((category, i) => (
          <button
            className='categoryAside-category'
            id={category}
            key={category}
            onClick={changeCurCategory}>
            <p className='cursor-pointer'>{category}</p>
            <p className='cursor-pointer'>
              {postCategoryCount[categories.indexOf(category)]}
            </p>
          </button>
        ))}
      </div>
    </section>
  );
};

export default CategoryAside;
